import { useMutation } from "react-query";
import { useNavigate } from "react-router-dom";
import { loginUser } from "../../api/api";
import { useAuthContext } from "../../context/AuthContext";


function Login() {
	const navigate = useNavigate();
	const { login } = useAuthContext()

	// const fetchData = useMutation(async (body) => {
	// 	return await loginUser(body)
	// }, {
	// 	onSuccess: (data) => {
	// 		if (data.message === 'success') {
	// 			login(data.userInfo)
	// 			navigate('/')
	// 		} else if (data.message === 'NoExist') {
	// 			alert('  .')
	// 			return
	// 		} else if (data.message === 'PwdFail') {
	// 			alert('  .')
	// 			return
	// 		}
	// 	}
	// })

	const handleLogin = async (e) => {
		e.preventDefault()
		let username = e.target.username.value
		let password = e.target.password.value

		let body = { username, password }
		let res = await loginUser(body)
		
		if (res.message === 'success') {
			login(res.userInfo)
			navigate('/')
		} else if (res.message === 'NoExist') {
			alert('  .')
			return
		} else if (res.message === 'PwdFail') {
			alert('  .')
			return
		}
	}

	return (
		<>
			<h3>Login</h3>
			<form onSubmit={handleLogin}>
				<div>
					<label htmlFor="username">Username</label>
					<input name="username" id="username" required></input>
				</div>
				<div>
					<label htmlFor="password">Password</label>
					<input type="password" name="password" id="password" autoComplete="none" required></input>
				</div>
				<div>
					<button type="submit">Login</button>
				</div>
			</form>
		</>
	)
}

export default Login